@import '../buttons';

.credit-card-form {
    width: 20vw;
    padding: 20px;
    margin-top: 30px;

    @media only screen and (min-width: 481px) and (max-width: 1000px) {
        width: 76vw;
        margin-left: 10px;
    }

    @media only screen and (max-width: 480px) {
        width: 76vw;
        margin-left: 10px;

    }

}

input.error {
    border-color: hsl(0, 100%, 66%);
}

.error-message {
    color: hsl(0, 100%, 66%);
    font-size: 12px;
    margin-top: 5px;
    display: block;
}


.form-group {
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
}

label {
    display: flex;
    font-size: 9px;
    font-weight: bold;
    margin-bottom: 5px;
    color: #333333;
}

input {
    padding: 7px;
    border: 1.5px solid rgb(204 204 204 / 50%);
    border-radius: 6px;
    font-size: 11px;

    &::placeholder {
        color: rgb(204 204 204 / 95%);
        font-weight: 400;

    }
}

.form-row {
    display: flex;
    justify-content: space-between;

    @media only screen and (min-width: 481px) and (max-width: 1000px) {
        flex-direction: row;
        gap: 10px;
    }

    @media only screen and (max-width: 480px) {
        flex-direction: row;
        gap: 10px;

    }
}

.exp-date-inputs {
    display: flex;
    gap: 10px;

    input {
        width: 20px;
    }

    @media only screen and (min-width: 481px) and (max-width: 1000px) {
        flex-direction: row;

    }

    @media only screen and (max-width: 480px) {
        flex-direction: row;
    }
}


